.graph {
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 547px;
    height: 191px;
}

.line {
    position: absolute;
    top: 0;
    bottom: 0;
    height: 1px;
    width: 100%;
    background: rgb(173, 172, 172);
    z-index: 26;
}


.bar-lines-container {
    display: flex;
    justify-content: space-evenly;
    position: relative;
    width: 100%;
    height: 90%;
    background: gray;
    align-items: flex-end;
}

.bar {
    width: 10%;
    height: 90%;
    background: green;
    z-index: 25;
    display: flex;
}

.xaxis {
    display: flex;
    position: relative;
    width: 100%;
    height: 10%;
    margin-top: 5px;
}

.label {
    display: flex;
    position: relative;
    width: 100%;
    height: 10%;
    margin-top: 5px;
    align-items: center;
    justify-content: center;
}