.flexboxes {
    display: flex;
}
  
.column {
    display: flex;
    flex-direction: column;
}

.background {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1024px;
    z-index: -55;
      
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
  
  /* Set up positioning */
    position: fixed;
    top: 0;
    left: 0;
  }